.c-product-tech-accordion__icon {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  display: block;
  width: 17px;
  height: 17px; }
  .c-product-tech-accordion__icon:after, .c-product-tech-accordion__icon:before {
    content: '';
    position: absolute;
    display: block;
    background-color: #333333; }
  .c-product-tech-accordion__icon:after {
    height: 3px;
    width: 100%;
    top: 7px;
    left: 0; }
  .c-product-tech-accordion__icon:before {
    height: 100%;
    width: 3px;
    top: 0;
    left: 7px;
    opacity: 1;
    transform: rotate(0);
    transition: opacity 0.1s ease-in-out 0.5s, transform 0.4s ease-in-out;
    transition-delay: 0s; }
  .is-active .c-product-tech-accordion__icon:before {
    opacity: 0;
    transform: rotate(-0.25turn);
    transition: opacity 0.1s ease-in-out 0.5s, transform 0.4s ease-in-out; }

